.cartitems {
    margin: 50px 110px;
}

.cartitems hr {
    height: 3px;
    background: #e2e2e2;
    border: 0;
}

.classitems-format-main {
    display: grid;
    grid-template-columns: 0.5fr 2fr 1fr 1fr 1fr 1fr;
    align-items: center;
    gap: 75px;
    padding: 20px 0px;
    color: #454545;
    font-size: 18px;
    font-weight: 600;


}
.cartitems-format{
    margin-left:10px;
    font-size: 17px;
    font-weight: 500;

}
.carticon-product-icon{
    height: 72px;
}
.cartitems-remove-items{
    width: 15px;
    margin: 0px 20px;
    cursor: pointer;
}
.cartitems-quantity{
    width: 64px;
    height:50px;
    border: 2px solid #ebebeb;
    background: #fff;
}
.cartitems-down{
    display: flex;
    margin: 100px 0px;
}
.cartitems-total{
    flex:1;
    display: flex;
    flex-direction: column;
    margin-right: 200px;
    flex-direction: column;
    gap: 40px;
}
.cartitems-total-items{
    display: flex;
    justify-content: space-between;
    padding: 15px 0px;
}
.cartitems-total button{
width: 262px;
height: 58px;
outline:none;
border: none;
background-color: #ff5a5a;
color: #fff;
font-size: 16px;
font-weight: 600;
cursor: pointer;
}
.cartitems-promocode{
    flex: 1;
    font-size: 15px;
    font-weight: 500;

}
.cartitems-promocode p{
    color: #555;
}
.cartitems-promobox{
    width: 504px;
    margin-top: 15px;
    padding-left: 20px;
    height: 58px;
    background-color: #eaeaea;
}
.cartitems-promobox input{
    outline: none;
    border: none;
    background-color: transparent;
    font-size: 16px;
    width: 330px;
    height: 50px;
}
.cartitems-promobox button{
    width: 170px;
    height: 58px;
    font-size: 16px;
    background: black;
    color: white;
    
    cursor: pointer;
}